/* تخصيص عام */
html {
  direction: rtl;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  background: linear-gradient(135deg, #f0f9ff 0%, #ffffff 100%);
  color: #0f172a;
  font-family: 'Tajawal', ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, "Helvetica Neue", Arial, "Noto Sans";
}

/* ✅ مكونات جاهزة */
.card {
  background: white;
  border: 1px solid #f1f5f9;
  border-radius: 0.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  font-weight: 500;
  border-radius: 0.375rem;
  transition: all 0.2s;
}

.btn-primary {
  color: white;
  background: #0284c7;
}

.btn-primary:hover {
  background: #0369a1;
}

.btn-secondary {
  color: white;
  background: #059669;
}

.btn-secondary:hover {
  background: #047857;
}

.btn-ghost {
  color: #374151;
}

.btn-ghost:hover {
  background: #f3f4f6;
}

.input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  outline: none;
}

.input:focus {
  ring: 2px;
  ring-color: #38bdf8;
}

.select {
  width: 100%;
  padding: 0.5rem 0.75rem;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  outline: none;
}

.select:focus {
  ring: 2px;
  ring-color: #38bdf8;
}

.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal-panel {
  width: 96%;
  max-width: 42rem;
  background: white;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  border: 1px solid #f1f5f9;
  padding: 1.5rem;
  text-align: right;
}

body.modal-open {
  overflow: hidden;
}


@keyframes fade {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fade {
  animation: fade 0.15s ease-out;
}

